body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.site-name {
  font-family: cursive;
}
.card {
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.2);
}

li {
  font-size: large;
  font-weight: 600;
}

.addItem {
  display: block;
}

.col-50 {
  display: inline-block;
  width: 35%;
}

.inline-block {
  display: inline-block;
}

input {
  margin: 12px 0px;
}
.plus {
  color: green;
  font-size: 35px;
  font-weight: 900;
  position: relative;
  top: 3px;
}

.error {
  text-align: center;
  color: #bf1650;
}

.error::before {
  display: inline-block;
  content: "\26A0    ";
}
@media screen and (max-width: 750px) {
  .col-50 {
    width: 100%;
  }
}

a,
a:hover {
  display: block;
  color: rgba(0, 0, 0, 0.7);
  text-align: center;
  text-decoration: none;
  padding: 6px;
  border-radius: 5px;
}

a:hover {
font-weight: bolder;

}

.delete-icon  {
text-align: right !important;
margin-right : 10px !important;

}

.desc{
  font-size: x-small;
}
body {
  background: white;
 }

form {
  max-width: 500px;
  margin: 0 auto;
}

h1 {
  font-weight: 100;
  color: black;
  text-align: center;
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(79, 98, 148);
}

.form {
  background: #0e101c;
  max-width: 400px;
  margin: 0 auto;
}

.error-message {
  color: #bf1650;
}

.error-message::before {
  display: inline;
  content: "\26A0   ";
}

input {
  display: block;
  box-sizing: border-box;
  width: 100%;
  border-radius: 4px;
  border: 1px solid black;
  padding: 10px 15px;
  margin-bottom: 10px;
  font-size: 14px;
}

label {
  line-height: 2;
  text-align: left;
  display: block;

  margin-top: 20px;
  color: black;
  font-size: 14px;
  font-weight: 500;
}

button[type="submit"],
input[type="submit"] {
  /* background: #ec5990; */
  color: black;
  text-transform: uppercase;
  border: none;
  margin-top: 40px;
  padding: 20px;
  font-size: 16px;
  font-weight: 100;
  letter-spacing: 10px;
}



button[type="submit"]:active,
input[type="button"]:active,
input[type="submit"]:active {
  transition: 0.3s all;
  -webkit-transform: translateY(3px);
          transform: translateY(3px);
  border: 1px solid black;
  opacity: 0.8;
}

input:disabled {
  opacity: 0.4;
}

input[type="button"]:hover {
  transition: 0.3s all;
}

button[type="submit"],
input[type="button"],
input[type="submit"] {
  -webkit-appearance: none;
}

.App {
  max-width: 300px;
  margin: 0 auto;
}

